/* .section {
  display: flex;
  align-content: center;
} */

.content {
  width: 100%;
  height: 100%;
  /* padding: 100px; */

  /* display: grid;
  grid-template-columns: 40% 40%;
  align-items: center;
  justify-content: center;
  gap: 80px; */

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;

  /* border: 2px solid red; */
}

.title {
  filter: brightness(1.6);
  text-decoration: underline;
  /* margin-bottom: 30px; */
  transform: translateY(-100px);
}

.left {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;

  /* height: 60vh; */
  /* border: 2px solid green; */
}

.skills {
  width: 100%;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.25) 3px 3px 3px;
  max-width: 330px;
}

.skills:hover {
  filter: brightness(1.05);
}

.link {
  width: 100%;
}

.button {
  width: 100%;
  /* max-width: 350px; */
  max-width: 330px;
  height: 80px;
  /* flex-grow: 2; */
  border-radius: 20px;
  background: #1380d7;
  color: white;
  font-size: 20px;
  font-weight: bold;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.25) 3px 3px 3px;
  /* max-height: 100px; */
  /* min-height: 70px; */
}

button:hover {
  /* background: #0e65ad; */
  cursor: pointer;
  filter: brightness(1.1);
}

.right {
  /* height: 60vh; */

  /* border: 2px solid green; */
}

.resume {
  /* display: block; */
  /* max-height: 60vh; */
  max-width: 370px;
  /* max-width: 400px; */
  box-shadow: rgba(0, 0, 0, 0.25) 3px 3px 6px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
}

.resume:hover {
  /* border: 3px solid red; */
  filter: brightness(1.05);
}

@media only screen and (max-width: 1300px) {
  .button {
    width: 280px;
    height: 70px;
    font-size: 16px;
  }

  .skills {
    width: 280px;
  }

  .resume {
    width: 310px;
  }
}

@media only screen and (max-width: 1100px) {
  .button {
    width: 230px;
    height: 70px;
    font-size: 16px;
  }

  .skills {
    width: 230px;
  }

  .resume {
    width: 270px;
  }
}

@media only screen and (max-width: 900px) {
  .content {
    display: flex;
    flex-direction: column;
    padding-top: 30px;
  }

  .title {
    transform: translateY(0);
    margin-bottom: 0;
  }

  .resume {
    width: 250px;
  }
}

@media only screen and (max-width: 600px) {
  .resume {
    width: 230px;
    /* margin-top: -30px; */
  }

  .button {
    margin-top: 20px;
  }
}
